.concard{
    box-shadow:0 0 20px rgba(0,0,0,0.2);
    padding:1rem 1rem ;
    margin:auto auto;
    border-radius: 15px;
    background: rgba(241, 237, 237, 0.5);
    width: 30rem;
    height:20rem;
    overflow:auto;
    position: relative;
    top:30%;
    /* justify-content:center; */
    /* left:32%; */
    /* right:33.33%; */
    z-index: 5;
}

.concard .welcome{
    font-weight:bold;
    font-size: large;
    /* width:90%; */
    text-align: center;
    padding:0.5rem 5rem 0.5rem 4rem;
    margin-left: 0 auto;
}
