.isarrowcont{
    width:100%;
    /* height:505px; */
    position: relative;
}
.leftarrow{
    position:absolute;
    top:50%;
    transform:translate(0,-50%);
    left:32px;
    font-size: 45px;
    background-color: rgba(0, 0, 0, 0.235);
    color:#fff;
    z-index: 4;
    cursor:pointer;
}
.rightarrow{
    position:absolute;
    top:50%;
    transform:translate(0,-50%);
    right:32px;
    font-size:45px;
    background-color: rgba(0, 0, 0, 0.235);
    color:#fff;
    z-index: 4;
    cursor:pointer;
}
