@media screen and (max-width: 900px){
    .card{
        box-shadow:0 0 20px rgba(0,0,0,0.2);
        padding:1rem 1rem ;
        border-radius: 15px;
        background: rgba(241, 237, 237, 0.5);
        max-width: 95%;
        height: 500px;
        overflow:auto;
        position: relative;
        top:3%;
        left:10px;
        z-index: 5;
    } 
    .scrollingpics{
        position: relative;
        max-width:95%;
        max-height:auto;
        top:0rem;
        left:0rem;
        box-shadow:0 0 20px rgba(0,0,0,0.2);
    object-fit:contain;
    overflow: hidden;
    }
    .scrollingpicscont{
        position:relative;
        height:auto;
        padding:1rem 0;
        top:6rem;
        left:0.5rem;
    }
    .scrollingpics img{
        object-fit: contain;
        height:auto;
        border-radius: 20px;
        
    }
    .isarrowcont{
        width:100%;
        height:auto;
        position: relative;
    }
    .welcome .introcont p{
        width: 100%;
    }
    .News{
        box-shadow:0 0 20px rgba(0,0,0,0.2);
        border-radius: 15px;
        background-color: #ffffffac;
        padding: 1rem 0.5rem;
        width:95%;
        height:20rem;
        overflow: hidden;
        position: static;
    }
    .NewsImagecontainer{
        width:100%;
        height:auto;
        padding:0rem 0;
        position:relative;
        top:5%;
    }
    .NICC{
        display:flex;
        flex-direction: column;
        justify-content:space-between;
        padding:2rem 0 0 1rem;
        border-radius:15px;
        /* padding:0.5rem 1rem; */
    }
    .labimg{
        max-width:45%;
        height:19rem;
        /* margin-left: 10.5rem; */
        /* justify-content: center; */
        padding:1rem 0;
        position: static;
        /* margin-left:auto; */
        top:1rem;
        left:1rem;
        box-shadow:0 0 0px rgba(0,0,0,0.2);
        background-color: #ffffff00;
        border-radius: 15px;
    }
    .labimg img{
        /* padding-right: 20px; */
        margin-left: 8%;
        width: 80%;
        /* height: 100%; */
        object-fit:cover;
        /* background-size:cover; */
    }
    .hero{
        height:2000px;
        width:100%;
        /* height:auto; */
    }
    .bgimg{ 
        width:100%;
        height: 2000px;
        object-fit: cover;
        background-size:cover;
        background-position:center;
        border-radius: 0px;
    }
