.header{
    display:flex;
    justify-content:flex-start;
    align-items: center;
    padding: 1rem;
    position:fixed;
    width:100%;
    height:50px;
    z-index:10;
}
.header-bg{
    background-color:rgb(255, 255, 255);
    transition: 0.5s;
}
/* .header h1{
    text-decoration:underline #ff0000;
    align-items:flex-start;
    margin-right: auto;
    top:0px;
    left:10px;
}*/
.title{
    padding-left: 0rem;
    width:auto;
    font-size: 2rem;
}
.title sup{
    font-size: small;
}
.logocont{
    height:50px;
    /* width: 50px; */
    padding-right:0.3rem;
    overflow: hidden;
}
.logo{
    width:2.2rem;
    padding:0.3rem 0rem 0 0;
object-fit: contain;
}
.nav-menu{
    display:flex;
    margin-left: auto;
}
.nav-menu li{
    padding:0 1rem;
}
.nav-menu li a{
    font-size:1.2rem;
    font-weight:500;
}
.hamburger{
    display:none;
}

@media screen and (max-width: 1040px){
    .nav-menu{
        flex-direction:column;
        justify-content:center;
        align-items:center;
        width:100%;
        height:100vh;
        position: absolute;
        top:0;
        left:-100%;
        background-color:aliceblue;
        opacity: 0.8;
        z-index:-3;
        transition:0.3s;
    }
    .nav-menu.active{
        left:0;
    }
    .nav-menu li{
        padding:1rem 0;
    }
    .nav-menu li a{
        font-size:2rem;
    }
    .hamburger{
        display:initial;
        margin-left: auto;
    }
    .title{
        padding-left: 0rem;
        width:auto;
        font-size: 1.4rem;
    }

}