.pubhero {
    padding:1rem 1rem 1rem 1rem;
    background-image: linear-gradient(to right, rgb(248, 251, 255) ,rgb(240, 199, 192));
}
.pubtitle{
    padding:0rem 1rem 0rem 1.75rem; 
}
.pubcont{
    width:100%;
    display:grid;
    grid-template-columns:repeat(1,1fr);
    grid-gap:8px;
    padding:0.5rem 1rem 1.5rem 1rem;
}

.pubcardcont{
    display:flex;
    justify-content:space-between;
    padding: 1rem 1rem;
    width:100%;
    /* height:30vh; */
    overflow:auto;
    box-shadow:0 0 10px rgba(0,0,0,0.2);
    border-radius: 10px;
}
.publeftcont{
    padding:0.35rem 1rem 0 0;
    font-weight: bold;
}

.pubright {
    padding:0.3rem 0;
    text-align:justify;
    font-size:medium;
}
.ttl{
    font-size:large;
    color:rgb(50, 32, 97);
}
.ttl:hover{
    text-decoration:underline;
}
.pubrightcont{
    margin-right:auto;
}
.pubright h3{
    padding:0 0 0 0;
}
.journal{
    padding:1.5rem 0 0.4rem 1.5rem;
}