.rehero .retitle{
    padding:1rem 1rem 1rem 1rem;
}
.rehero{
    background-image: linear-gradient(to right, rgb(248, 251, 255) ,rgb(192, 207, 240));
}
.relist {
    list-style-type:none;
}
.recont{
    width:100%;
    display:grid;
    grid-template-columns:repeat(2,1fr);
    grid-gap:40px;
    padding:1rem 1rem 3rem 1rem;
}

.recardcont{
    display:flex;
    justify-content:space-between;
    padding: 1rem 1rem;
    width:100%;
    /* height:30vh; */
    overflow:auto;
    box-shadow:0 0 20px rgba(0,0,0,0.2);
    border-radius: 15px;
}
.releft img{
    width:200px;
    padding:0rem 1rem 0 0;
}
.reright h2{
    padding:0 0 0.5rem 0;
}
.reright li{
    padding:0.5rem 0;
    text-align:left;
    font-size:large;
}

.reright p{
    word-wrap: break-word;
}
.rerightcont{
    padding:0rem 1rem;
    margin-right: auto;
}
@media screen and (max-width: 1000px) {
    .recont{
        width:100%;
        display:grid;
        grid-template-columns:repeat(1,1fr);
        grid-gap:40px;
        padding:1rem 1rem 3rem 1rem;
    }
}