.pplhero {
    padding:0rem 1rem 1rem 1rem;
    background-image: linear-gradient(to right, rgb(248, 251, 255) ,rgb(240, 238, 192));
    /* background-repeat: no-repeat;
    background-size:auto 100%; */
}
.ppltitle{
    padding:1rem 1rem 1rem 1rem;
}
.ppltitle h2{
    padding-top: 1rem;
}
.ppltitle p{
    font-size:x-large;
    font-style:bold;
}
.pplimgcont{
    width:100%;
    display:grid;
    grid-template-columns:repeat(2, 1fr);
    grid-gap:40px;
    padding-bottom: 5rem;
    padding-left: 1rem;
}

.pplimgcardcont{
    display: flex;
    justify-content: space-between;
    padding: 1rem 1rem;
    width:100%;
    height:40vh;
    overflow:auto;
    background-color: whitesmoke;
    box-shadow:0 0 20px rgba(0,0,0,0.2);
    border-radius: 15px;
}
.pplleft{
    width:150px;
    height:180px;
    padding:1rem 1rem 0 0;
}
.pplavatar{
    width:100%;
    height:180px;
}
.pplright p{
    text-align: justify;
    padding:rem 0;
}
.pplright {
    padding:1rem 0;
}
.ppliconscont{
    width: 100%;
    padding-left: 85%;
}
.ppliconscont{
    width: 100%;
    padding-left:55%;
    justify-content: space-between;
}
.pplicons{
    display: flex;
    padding:0 0rem;
    justify-content: space-between;
}
.principalinv{
    width:100%;
    /* display:grid;
    grid-template-columns:repeat(2, 1fr);
    grid-gap:40px; */
    padding-bottom: 3rem;
    padding-left: 1rem;
}
.princiimgcont{
    display: flex;
    justify-content: space-between;
    padding: 1rem 1rem;
    width:59%;
    height:67vh;
    overflow:auto;
    box-shadow:0 0 20px rgba(0,0,0,0.2);
    background-color: whitesmoke;   
    border-radius: 15px;
}
.princileft{
    width:150px;
    height:180px;
    padding:1rem 1rem 0 0;
}
.princiavatar{
    width:100%;
}
.princiright p{
    text-align: justify;
}
.princiiconscont{
    width: 100%;
    padding-left:55%;
    justify-content: space-between;
}
.princiicons{
    display: flex;
    padding:0 0rem;
    justify-content: space-between;
}
.currentstud h2{
    padding: 0 1rem 1rem 1rem;
}
.prevstud h2{
    padding: 0 1rem 1rem 1rem;
}
.prevlist{
    padding:0 0 1rem 1rem;
}
@media screen and (max-width: 1000px) {
    .princiimgcont{
        display: flex;
        justify-content: space-between;
        padding: 1rem 1rem;
        width:100%;
        height:30rem;
        overflow:auto;
        box-shadow:0 0 20px rgba(0,0,0,0.2);
        background-color: whitesmoke;   
        border-radius: 15px;
    }
    .pplimgcont{
        width:100%;
        display:grid;
        grid-template-columns:repeat(1,1fr);
        grid-gap:40px;
        padding-bottom: 5rem;
        padding-left: 1rem;
    }
    .pplimgcardcont{
        display: flex;
        justify-content: space-between;
        padding: 1rem 1rem;
        width:100%;
        height:24rem;
        overflow:auto;
        background-color: whitesmoke;
        box-shadow:0 0 20px rgba(0,0,0,0.2);
        border-radius: 15px;
    }
}