@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700;800;900&display=swap');

*{
  box-sizing: border-box;
  margin:0;
  padding:0;
  font-family:"Outfit", sans-serif;
}

body{
  background:#ffffff;
}
h1,h4,p, a{
  color:#000000;
  text-decoration: none;
}
ul{
  list-style-type: none;
}
.btn{
  padding:12px 32px;
  font-size:1rem;
  text-transform:uppercase;
  background:black;
  color: #ffffff;
  border:1px solid #0c0c0c;
  font-weight:600;
  cursor: pointer;
}
.shad{
  box-shadow: 0 8px 16px 0 rgba(0.5,0,0.2,0.2), 0 0 20px 0 rgba(0,0,0,0.19);
}
.btn-light{
  background:transparent;
  color:#000000
}
.btn:hover{
  background:#ff0000;
  color:#e2e2d1;
  transition:0.3s;
}
