.collabhero .collabtitle{
    padding:0rem 1rem 1rem 1rem;
}
.collabhero{
    background-image :linear-gradient(to right, rgb(248, 251, 255) ,rgb(227, 245, 207));
}

.collabimgcont{
    width:100%;
    display:grid;
    grid-template-columns:repeat(3, 1fr);
    grid-gap:20px;
    padding-bottom: 2rem;
    padding-left: 1rem;
}

.collabimgcardcont{
    display: flex;
    justify-content: space-between;
    padding: 1rem 1rem;
    width:100%;
    height:380px;
    overflow:auto;
    box-shadow:0 0 20px rgba(0,0,0,0.2);
    background-color: whitesmoke;
    border-radius: 15px;
}
.collableft{
    width:150px;
    height:200px;
    padding:1rem 1rem 0 0;
}
.collabavatar{
    width:100%;
}
