.collabiconscont{
    width: 100%;
    padding-left:70%;
    justify-content:flex-end;
}
.collabicons{
    display:flex;
    padding:0 0rem;
    
    /* justify-content: space-between; */
}
.collabicons a{
margin-left: auto;
}
.collabrightcont p  {
    text-align: left;
}
@media screen and (max-width: 1000px) {
    .collabimgcont{
        width:100%;
        display:grid;
        grid-template-columns:repeat(1, 1fr);
        grid-gap:20px;
        padding-bottom: 2rem;
        padding-left: 1rem;
        padding-right:1rem;
    }
    
}